@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  margin: 0;
  /*margin used to make space between elements (out border)*/
  padding: 0;
  /*used to create space between the contact on the element (inborder)*/
  box-sizing: border-box;
  /* controls the size of width and height*/
  font-family: "Poppins", sans-serif;
  /* used for font type*/
}

body {
  background-color: #081b29;
  color: #ededed;
}

img {
  position: absolute;
  right: 150px;
  height: 450px;
  top: 200px;
  opacity: 0.8;
}


